body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  padding: 0 1rem;
}

.content {
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.movies {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
}
.movie{
  display: flex;
  flex-direction: column;
}
.card-content{
  margin-top: auto;
}
.search-panel{
  position: relative;
  display: flex;
  align-items: center;
}
.search-input{
  
}
.filter-panel label{
  margin-right: 2rem;
}

.search-btn {
    position: absolute;
    top: 0;
    right: 0.5rem;
}
.header-logo{
  white-space: nowrap;
}
